import React, { useState, useEffect } from 'react'
import { useQuery, useMutation } from '@apollo/client'
import {USERS,CODE} from './queries'
import {Table,Spinner} from 'react-bootstrap'

const UserPagePrivate = ()=> {
	const usersResult = useQuery(USERS)
	const codeResult = useQuery(CODE)
	if(codeResult.loading || usersResult.loading){
		return (<Spinner animation="border" variant="success" />)
	}
	const users = usersResult.data.users
	const code = codeResult.data.getCode
	return (<div>
	<p className="h5">{`رمز التفعيل الحالي هو: ${code}`}</p>
	<h4>{"بيانات المستخدمين"}</h4>
		{users.map(user => {return(
		<Table className='mb-3' striped bordered hover>
			<tbody>
			<tr>
			<td>{"اسم المستخدم"}</td>
			<td>{user.username}</td>
			</tr>
			<tr>
			<td>{"رقم الجوال"}</td>
			<td>{user.phone}</td>
			</tr>
			<tr>
			<td>{"تاريخ إنشاء الحساب"}</td>
			<td>{user.date}</td>
			</tr>
			</tbody>
		</Table>
		)})}
	</div>)
}

export default UserPagePrivate