import React, { useState, useEffect } from 'react'
import { useQuery } from '@apollo/client'
import ModalImage from "react-modal-image"
import {ONE_VILLA,ME} from './queries'
import ReactWhatsapp from 'react-whatsapp'
import {useParams} from 'react-router-dom'
import {Table} from 'react-bootstrap'
import ShowVillaPrivate from './ShowVillaPrivate'
import Options from './Options'
const ShowVilla = ()=> {
	const id = useParams().id
	const result = useQuery(ONE_VILLA,{variables:{id:id}})
	const meResult= useQuery(ME)
	if(result.loading || meResult.loading){
	  return (<div>Loading</div>)
    }
    const villa = result.data.allVillas[0]
	const me = meResult.data.me
	if(!villa){
		return <div>{"حدث خطأ"}</div>
	}
    return(<div>
	
	<ModalImage className="show-image mb-5"
		small={villa.displayImage}
		large={villa.displayImage}
		alt="صورة العرض"
	/>
	<div className='information-container mb-5'>
	<h4>{`الحي:${villa.district}`}</h4>
	<h4>{`الفئة:${villa.category}`}</h4>
	<h4>{`الحجم:${villa.size}م`}</h4>
	<h4>{`السعر:${villa.price}ريال`}</h4>
	</div>
	{villa.imagesOfVilla[0] && <div>
	<h2>{"صور الفيلا"}</h2>
		<div className="flex-container">
		{villa.imagesOfVilla.map(image => <ModalImage className="flex-item-img" 
		  small={image}
		  large={image}
		/>)}
		</div>
	</div>}
	{villa.imagesOfRooms[0] && <div>
	<h2>{"صور الغرف"}</h2>
		<div className="flex-container">
		{villa.imagesOfRooms.map(image => <ModalImage className="flex-item-img" 
		  small={image}
		  large={image}
		/>)}
		</div>
	</div>}
	{villa.imagesOfKitchen[0] && <div>
	<h2>{"صور المطبخ"}</h2>
		<div className="flex-container">
		{villa.imagesOfKitchen.map(image => <ModalImage className="flex-item-img" 
		  small={image}
		  large={image}
		/>)}
		</div>
	</div>}
	{villa.imagesOfBathrooms[0] && <div>
	<h2>{"صور دورات المياه"}</h2>
		<div className="flex-container">
		{villa.imagesOfBathrooms.map(image => <ModalImage className="flex-item-img" 
		  small={image}
		  large={image}
		/>)}
		</div>
	</div>}
	{villa.imagesOfPlan[0] && <div>
	<h2>{"صور مخطط البناء"}</h2>
		<div className="flex-container">
		{villa.imagesOfPlan.map(image => <ModalImage className="flex-item-img" 
		  small={image}
		  large={image}
		/>)}
		</div>
	</div>}
	{(villa.notes || villa.notes !== '') && <p className="h5 m-5">{villa.notes}</p>}
	<h2 className="mb-4">{"معلومات إضافية"}</h2>
	<Table className='mb-3' striped bordered hover>
	<tbody>
	<tr>
	<td>{"المساحة"}</td>
	<td>{villa.size}</td>
	</tr>
	<tr>
	<td>{"عدد الغرف"}</td>
	<td>{villa.noOfRooms}</td>
	</tr>
	<tr>
	<td>{"عدد الصالات"}</td>
	<td>{villa.noOfHalls}</td>
	</tr>
	<tr>
	<td>{"عدد دورات المياه"}</td>
	<td>{villa.noOfBathrooms}</td>
	</tr>
	<tr>
	<td>{"عرض الشارع"}</td>
	<td>{villa.streetWidth}</td>
	</tr>
	<tr>
	<td>{"عدد الشوارع"}</td>
	<td>{villa.noOfStreets}</td>
	</tr>
	<tr>
	<td>{"الغرض"}</td>
	<td>{villa.landType}</td>
	</tr>
	<tr>
	<td>{"عمر العقار (بالسنين)"}</td>
	<td>{villa.age === 0 ? "جديد" : villa.age}</td>
	</tr>
	</tbody>
	</Table>
	<div className='container text-center'>
	{(me &&( me.id === villa.userCreated && me.userType === 'admin')) && <ShowVillaPrivate villa={villa}/> }
	<ReactWhatsapp className='p-4 btn btn-success center' 
	number={Options.phone} message="Hello World!!!">{"تواصل عبر الواتس"}</ReactWhatsapp>
	</div>
	</div>
	
	)
}

export default ShowVilla