import React, { useState } from 'react'
import {useMutation } from '@apollo/client'
import {Button } from 'react-bootstrap'
import {Link} from 'react-router-dom'
import {SET_SHOW} from './queries'


const Land = ({land,type,history})=> {
		const [toggle,setToggle] = useState(land.show)
		const [setShow,result] = useMutation(SET_SHOW)
		console.log(land)
		const state = land.done ? "مغلق" : "مفتوح"
		const titles = {
			land:"أرض",
			villa:"فيلا",
			building:"عمارة"
		} 
		const handleToggle = ()=>{
			setShow({variables:{property:type,id:land.id}})
			setToggle(!toggle)
		}
		return(
	    <Link to={`${history.location.pathname}/${land.id}`} className="text-link">
		<div className='flex-item'>
		<div className='purpose-header p-1'>
		<h3>{`${titles[type]} ${land.purpose}`}</h3>
		</div>
		<img src={land.displayImage} />
		
		<div className='text-container'>
		<h4>{`الحي:${land.district}`}</h4>
		<h4>{`الفئة:${land.category}`}</h4>
		<h4>{`الحجم:${land.size}م`}</h4>
		<h4>{`السعر:${land.price}ريال`}</h4>
		<h4 className={land.done ? "text-danger":"text-primary"}>{state}</h4>
		<Button className="px-4"variant={toggle ? "secondary":"primary"} onClick={handleToggle}>{toggle ? "إخفاء":"إظهار"}</Button>
		</div>
		
		</div> 
	  </Link>
	  
      
		)
}

export default Land