import React, { useState, useEffect } from 'react'
import { useQuery, useMutation } from '@apollo/client'
import {ONE_LAND_PRIVATE,SET_DONE,DELETE_LAND} from './queries'
import {Table,Button} from 'react-bootstrap'
import Swal from 'sweetalert2'
import { useHistory } from 'react-router-dom'

const ShowLandPrivate = ({land})=> {
	const history = useHistory()
	const [toggle,setToggle] = useState(land.done)
	const [setDone,doneResult] = useMutation(SET_DONE)
	const [deleteLand, deleteResult] = useMutation(DELETE_LAND)
	const result = useQuery(ONE_LAND_PRIVATE,{variables:{id:land.id}})
	const swalWithBootstrapButtons = Swal.mixin({
		customClass: {
			confirmButton: 'btn btn-danger',
			cancelButton: 'btn btn-success'
		},
		buttonsStyling: false
	})
	const handleToggle = ()=>{
			setDone({variables:{property:"land",id:land.id}})
			setToggle(!toggle)
		}
	if(result.loading){
	  return (<div>Loading</div>)
    }
	const handleDelete = ()=>{
		

	swalWithBootstrapButtons.fire({
		title: 'هل أنت متاكد من رغبتك في حذف العقار؟',
		text: "لن يمكنك استعادته بعد الحذف",
		icon: 'warning',
			showCancelButton: true,
			confirmButtonText: 'نعم, قم بالحذف!',
			cancelButtonText: 'لا, قم بالإلغاء',
			reverseButtons: true
			}).then((result) => {
		if (result.isConfirmed) {
			deleteLand({variables:{id:land.id}})
			swalWithBootstrapButtons.fire(
			'تم الحذف!',
			'تم حذف العقار بنجاح',
			'success'
		)
		history.push('/')
		} else if (
		result.dismiss === Swal.DismissReason.cancel
		) {
		swalWithBootstrapButtons.fire(
		  'تم الإلغاء',
		  'لم يتم حذف العقار',
		  'error'
		)
	  }
	})
	}
	const privateLand = result.data.allLands[0]
	return (
	<div id="private">
	<h2 className="float-right">{"معلومات خاصة"}</h2>
	<Table className='mb-3' striped bordered hover>
	<tbody>
	<tr>
	<td>{"اسم المالك"}</td>
	<td>{privateLand.ownerName}</td>
	</tr>
	<tr>
	<td>{"رقم جوال المالك"}</td>
	<td>{privateLand.ownerPhone}</td>
	</tr>
	<tr>
	<td>{"حالة البيع"}</td>
	<td>{privateLand.done? "تم البيع" : "لم يتم البيع"}</td>
	</tr>
	</tbody>
	</Table>
	<Button className="px-4 m-3" variant={toggle ? "primary":"secondary"} onClick={handleToggle}>{toggle ? "أعد النشر":"تعليم كمبيع"}</Button>
    <Button className="px=4 m-3" variant='danger' onClick={handleDelete}>{"حذف الأرض"}</Button>
	</div>
	)
}

export default ShowLandPrivate