import React from 'react'
import {Pagination} from 'react-bootstrap'
import { useHistory } from 'react-router-dom'

function getPosition(string, subString, index) {
  return string.split(subString, index).join(subString).length;
}

const Pages = ({page,noOfPages}) => {
	const history = useHistory()
	const path = history.location.pathname
	let startPage,endPage 
	if (noOfPages <= 5) {
		startPage = 1
		endPage = noOfPages
	}
	else {
		if(page <= 3){
			startPage = 1 
			endPage = 5
		}
		else if (page + 2 >= noOfPages){
			startPage = noOfPages - 4
			endPage = noOfPages
		} else {
			startPage = page - 2
			endPage = page + 2
		}
	}
	
	let pages = [...Array((endPage + 1) - startPage).keys()].map(i => startPage + i);
	
	
	const handlePage = (currentPage)=>{
		const searchIndex = getPosition(path,'&',2)
		const searchPath = path.slice(searchIndex + 1)
		const newPath = path.split('=')[0] +'='+currentPage + "&" + searchPath
		history.push(newPath)
	}
	if(noOfPages < 2) {
		return null
	}
	return(
	<div>
	<Pagination className="flex-wrap" >
	<Pagination.Item disabled={page === 1} onClick={()=>{{handlePage(1)}}}>{"البداية"}</Pagination.Item>
	<Pagination.Item disabled={page === 1} onClick={()=>{{handlePage(page - 1)}}}>{"السابق"}</Pagination.Item>
	{pages.map(currentPage => {
		return( <Pagination.Item activeLabel={''} active={page === currentPage}
		onClick={()=>{if(currentPage !== page){handlePage(currentPage)}}}
		>{currentPage}</Pagination.Item>)
	})}
	<Pagination.Item disabled={page === noOfPages} onClick={()=>{{handlePage(page + 1)}}}>{"التالي"}</Pagination.Item>
	<Pagination.Item disabled={page === noOfPages} onClick={()=>{{handlePage(noOfPages)}}}>{"الأخير"}</Pagination.Item>
	</Pagination>
	</div>
	)
}

export default Pages