import React, { useState, useEffect } from 'react'
import { useQuery } from '@apollo/client'
import ModalImage from "react-modal-image"
import {ONE_LAND,ME} from './queries'
import ReactWhatsapp from 'react-whatsapp'
import {useParams} from 'react-router-dom'
import {Table} from 'react-bootstrap'
import ShowLandPrivate from './ShowLandPrivate'
import Options from './Options'
const ShowLand = ()=> {
	const id = useParams().id
	const result = useQuery(ONE_LAND,{variables:{id:id}})
	const meResult= useQuery(ME)
	if(result.loading || meResult.loading){
	  return (<div>Loading</div>)
    }
    const land = result.data.allLands[0]
	const me = meResult.data.me
	if(!land){
		return <div>{"حدث خطأ"}</div>
	}
    return(<div>
	
	<ModalImage className="show-image mb-5"
		small={land.displayImage}
		large={land.displayImage}
		alt="صورة العرض"
	/>
	<div className='information-container mb-5'>
	<h4>{`الحي:${land.district}`}</h4>
	<h4>{`الفئة:${land.category}`}</h4>
	<h4>{`الحجم:${land.size}م`}</h4>
	<h4>{`السعر:${land.price}ريال`}</h4>
	</div>
	{land.images[0] && <div>
	<h2>{"صور الأرض"}</h2>
	<div className="flex-container">
	{land.images.map(image => <ModalImage className="flex-item-img" 
	  small={image}
	  large={image}
	/>)}
	</div>
	</div>}
	{(land.notes || land.notes !== '') && <p className="h5 m-5">{land.notes}</p>}
	<h2 className="mb-4">{"معلومات إضافية"}</h2>
	<Table className='mb-3' striped bordered hover>
	<tbody>
	<tr>
	<td>{"المساحة"}</td>
	<td>{land.size}</td>
	</tr>
	<tr>
	<td>{"الطول"}</td>
	<td>{land.height}</td>
	</tr>
	<tr>
	<td>{"العرض"}</td>
	<td>{land.width}</td>
	</tr>
	<tr>
	<td>{"عرض الشارع"}</td>
	<td>{land.streetWidth}</td>
	</tr>
	<tr>
	<td>{"عدد الشوارع"}</td>
	<td>{land.noOfStreets}</td>
	</tr>
	<tr>
	<td>{"الغرض"}</td>
	<td>{land.landType}</td>
	</tr>
	</tbody>
	</Table>
	<div className='container text-center'>
	{(me &&( me.id === land.userCreated && me.userType === 'admin')) && <ShowLandPrivate land={land}/>}
	<ReactWhatsapp className='p-4 btn btn-success center' 
	number={Options.phone} message="Hello World!!!">{"تواصل عبر الواتس"}</ReactWhatsapp>
	</div>
	</div>
	
	)
}

export default ShowLand