import React, { useState, useEffect } from 'react'
import { useMutation } from '@apollo/client'
import { CREATE_USER} from './queries'
import { useHistory } from 'react-router-dom'
import {Form,Button} from 'react-bootstrap'
import Swal from 'sweetalert2'

const RegisterForm = ()=> {
	const [phone, setPhone] = useState('')
	const [username, setUsername] = useState('')
	const [code, setCode] = useState('')
	const [password, setPassword] = useState('')
	const history = useHistory()
	const [register, result] = useMutation(CREATE_USER)
	
	useEffect(() => {
    if ( result.data ) {
	  Swal.fire(
		'تم التسجيل بنجاح!',
		'يمكنك الآن تسجيل الدخول باستخدام رقم الجوال',
		'success'
      )
	  history.push("/login")
    }
  }, [result.data]) 
	
	const submit = async (event) => {
    event.preventDefault()
    try {
    await register({ variables: { phone,username,code,password } })
	} catch(error){
		Swal.fire({
			icon: 'error',
			title: 'حدث خطأ',
			text: error.message
		})
	}
	setPhone('')
	setPassword('')
	setCode('')
	setUsername('')
  }
	return(
	<div>
      <Form onSubmit={submit}>
	  <Form.Group className="m-2">
        <Form.Label>{"رقم الجوال"}</Form.Label>
          <Form.Control
		    required
            value={phone}
            onChange={({ target }) => setPhone(target.value)}
          />
      </Form.Group>
	  <Form.Group className="m-2">
        <Form.Label>{"اسم المستخدم"}</Form.Label>
          <Form.Control
		    required
            value={username}
            onChange={({ target }) => setUsername(target.value)}
          />
      </Form.Group>
	  <Form.Group className="m-2">
        <Form.Label>{"كلمة السر"}</Form.Label>
           <Form.Control
		    required
            type='password'
            value={password}
            onChange={({ target }) => setPassword(target.value)}
          />
      </Form.Group>
	  <Form.Group className="m-2">
        <Form.Label>{"رمز تفعيل الحساب"}</Form.Label>
           <Form.Control
		    required
            type='password'
            value={code}
            onChange={({ target }) => setCode(target.value)}
          />
      </Form.Group>
        <Button className="m-2" type='submit'>{"تسجيل حساب جديد"}</Button>
      </Form>
    </div>
	)
}


export default RegisterForm 