import React, { useState, useEffect } from 'react'
import { useMutation } from '@apollo/client'
import { LOGIN} from './queries'
import { useHistory } from 'react-router-dom'
import {Form,Button} from 'react-bootstrap'
import Swal from 'sweetalert2'

const LoginForm = ({setToken}) => {
  const [phone, setPhone] = useState('')
  const [password, setPassword] = useState('')
  const history = useHistory()
  const [ login, result ] = useMutation(LOGIN)
  useEffect(() => {
    if ( result.data ) {
      const token = result.data.login.value
	  setToken(token)
      localStorage.setItem('officeToken', token)
	  history.push("/")
	  
    }
  }, [result.data,setToken]) 
  
  
  

  const submit = async (event) => {
    event.preventDefault()
	try {
    await login({ variables: { phone, password } })
	} catch(error) {
		Swal.fire({
			icon: 'error',
			title: 'حدث خطأ',
			text: 'تأكد من صحة البيانات المدخلة'
		})
	}
	setPhone('')
	setPassword('')
  }

  return (
    <div>
      <Form onSubmit={submit}>
	  <Form.Group className="m-2">
        <Form.Label>{"رقم الجوال"}</Form.Label>
          <Form.Control
		    required
            value={phone}
            onChange={({ target }) => setPhone(target.value)}
          />
	  </Form.Group>
      <Form.Group className="m-2">
        <Form.Label>{"كلمة السر"}</Form.Label>
           <Form.Control
		    required
            type='password'
            value={password}
            onChange={({ target }) => setPassword(target.value)}
          />
      </Form.Group>
        <Button className="m-2" type='submit'>{"تسجيل الدخول"}</Button>
      </Form>
	  <p className="m-3 h5">{"أو"}</p>
	  <a href="/Register" className="m-3 link-primary h5">{"تسجيل حساب جديد"}</a>
    </div>
  )
}

export default LoginForm