import React, { useState,useEffect } from 'react'
import {useApolloClient} from '@apollo/client'
import LoginForm from './components/LoginForm'
import RegisterForm from './components/RegisterForm'
import AddLandForm from './components/AddLandForm'
import AddVillaForm from './components/AddVillaForm'
import AddBuildingForm from './components/AddBuildingForm'
import ShowLands from './components/ShowLands'
import ShowVillas from './components/ShowVillas'
import ShowBuildings from './components/ShowBuildings'
import ShowLand from './components/ShowLand'
import ShowVilla from './components/ShowVilla'
import ShowBuilding from './components/ShowBuilding'
import ShowMyLands from './components/ShowMyLands'
import ShowMyVillas from './components/ShowMyVillas'
import ShowMyBuildings from './components/ShowMyBuildings'
import NavbarMenu from './components/Navbar'
import UserPage from './components/UserPage'
import MainPage from './components/MainPage'
import './style.css'
import {
  BrowserRouter as Router,
  Switch, Route, Link,useParams,useHistory
} from "react-router-dom"




const  App = () =>{
	const [token, setToken] = useState(null)
	const client = useApolloClient()
	
	useEffect(() => {
    const token = localStorage.getItem('officeToken')
    if (token) {
      setToken(token)
    }
  }, [])
  
   const logout = () => {
	setToken(null)
	localStorage.clear()
	client.resetStore()
	
  }
  
  return (
	<div>
    <Router>
      <NavbarMenu token={token} logout={logout}/>
      <Switch >
	  <Route path='/login'>
	  <LoginForm setToken={setToken}/>
	  </Route>
	  <Route path='/register'>
	  <RegisterForm/>
	  </Route>
	  <Route path='/userPage'>
	  <UserPage/>
	  </Route>
	  <Route path='/lands:search/:id'>
	  <ShowLand/>
	  </Route>
	  <Route path='/villas:search/:id'>
	  <ShowVilla/>
	  </Route>
	  <Route path='/buildings:search/:id'>
	  <ShowBuilding/>
	  </Route>
	  <Route path='/myLands:search/:id'>
	  <ShowLand/>
	  </Route>
	  <Route path='/lands:search'>
	  <ShowLands/>
	  </Route>
	  <Route path='/villas:search'>
	  <ShowVillas/>
	  </Route>
	  <Route path='/buildings:search'>
	  <ShowBuildings/>
	  </Route>
	  <Route path='/myLands:search'>
	  <ShowMyLands/>
	  </Route>
	  <Route path='/myVillas:search'>
	  <ShowMyVillas/>
	  </Route>
	  <Route path='/myBuildings:search'>
	  <ShowMyBuildings/>
	  </Route>
	  <Route path='/addLand'>
	  <AddLandForm/>
	  </Route>
	  <Route path='/addVilla'>
	  <AddVillaForm/>
	  </Route>
	  <Route path='/addBuilding'>
	  <AddBuildingForm/>
	  </Route>
	  <Route path='/'>
	  <MainPage/>
	  </Route>
	  </Switch>
	  
    </Router>
	</div>
  )
}

export default App;
