import React, { useState, useEffect } from 'react'
import { useQuery,useMutation } from '@apollo/client'
import { MY_VILLAS,MY_VILLAS_PAGES,SET_SHOW} from './queries'
import {Card,Button,Spinner } from 'react-bootstrap'
import {Link,useHistory, useParams} from 'react-router-dom'
import SearchForm from './SearchForm'
import Pages from './Pages'
import Land from './Land'
import Accordion from 'react-bootstrap/Accordion'

const ShowMyVillas = ()=>{
	const history = useHistory()
	const search = useParams().search
	let page = 1
	const type = 'villa'
	const variables = {}
	let isValid = false
	const regexPage=/&page=[1-9][0-9]*&.*/g
	const regex = /&search(&district=[ء-ي0-9]+)?(&category=[0-9ء-ي]+)?(&landNo=[0-9]+)?(&landType=[ء-ي/])?(&sortBy=(price|size))?(&order=(asc|desc))?/g;
	if(regexPage.test(search)){
		const pageSlice = search.split("&")[1]
		variables[pageSlice.split('=')[0]] = Number(pageSlice.split('=')[1]) 
		page = variables['page']
		if(regex.test(search)){
		 isValid = true
		}
	}
	if(isValid && search.length > 7){
		const array = search.split("&").slice(3)
		array.map(a => variables[a.split("=")[0]]=a.split("=")[1])
	}
	
	const villasResult = useQuery(MY_VILLAS,{variables:{...variables}})
	const pagesResult = useQuery(MY_VILLAS_PAGES,{variables:{...variables}})
    if(villasResult.loading || pagesResult.loading ){
	  return (<div className="container">
		<Spinner animation="border" variant="success" />
	  </div>)
    }
    
	const villas = villasResult.data.myVillas
	const pages = pagesResult.data.noOfPagesMyVillas
	
	if(!villas[0]){
		return (<div className="container">
		<h1 className="mb-5">{"قائمة الفلل"}</h1>
		<p className="h4">{"لا توجد عقارات للعرض"}</p>
		</div>
		)
	}
	
	return (
	<div className="container" >
	<h1 className="mb-5">{"قائمة الفلل"}</h1>
	<Accordion defaultActiveKey="1" className="mb-5 allign-middle" >
  <Card bg="success" text="light" >
    <Accordion.Toggle as={Card.Header} eventKey="0" variant="primary">
		{"خيارات البحث"}
    </Accordion.Toggle>
    <Accordion.Collapse eventKey="0">
      <Card.Body>
				<SearchForm/>
				</Card.Body>
			</Accordion.Collapse>
		</Card>
	</Accordion>
	<div  className="flex-container">
		{villas.map( (villa) => <Land land={villa} type={type} history={history} />)}
	</div>
	<Pages page={page} noOfPages={pages}/>
	</div>
	)	
}

export default ShowMyVillas