import React, { useState, useEffect } from 'react'
import { useQuery } from '@apollo/client'
import { ALL_LANDS,ALL_LANDS_PAGES} from './queries'
import {Card,CardGroup, Row, Col, Container,Spinner } from 'react-bootstrap'
import {Link,useHistory, useParams} from 'react-router-dom'
import SearchForm from './SearchForm'
import Pages from './Pages'
import Accordion from 'react-bootstrap/Accordion'


const ShowLands = ()=>{
	const history = useHistory()
	const search = useParams().search
	let page = 1
	const variables = {}
	let isValid = false
	console.log(search)
	const regexPage=/&page=[1-9][0-9]*&.*/g
	const regex = /&search(&district=[ء-ي0-9]+)?(&category=[0-9ء-ي]+)?(&landNo=[0-9]+)?(&landType=[ء-ي/])?(&sortBy=(price|size))?(&order=(asc|desc))?/g;
	if(regexPage.test(search)){
		const pageSlice = search.split("&")[1]
		variables[pageSlice.split('=')[0]] = Number(pageSlice.split('=')[1]) 
		page = variables['page']
		if(regex.test(search)){
		 isValid = true
		}
	}
	console.log(variables)
	if(isValid && search.length > 7){
		const array = search.split("&").slice(3)
		array.map(a => variables[a.split("=")[0]]=a.split("=")[1])
	}
	const landsResult = useQuery(ALL_LANDS,{variables:{...variables}})
	const pagesResult = useQuery(ALL_LANDS_PAGES,{variables:{...variables}})
	if(landsResult.loading || pagesResult.loading){
	  return (<div className="container">
		<Spinner animation="border" variant="success" />
	  </div>)
  }
    
    
	const lands = landsResult.data.allLands
	const pages = pagesResult.data.noOfPagesLands
	
	if(!lands[0]){
		return (<div className="container">
		<h1 className="mb-5">{"قائمة الأراضي"}</h1>
		<p className="h4">{"لا توجد عقارات للعرض"}</p>
		</div>
		)
	}
	return (
	<div className="container" >
	<h1 className="mb-5">{"قائمة الأراضي"}</h1>
	<Accordion defaultActiveKey="1" className="mb-5 allign-middle" >
  <Card bg="success" text="light" >
    <Accordion.Toggle as={Card.Header} eventKey="0" variant="primary">
		{"خيارات البحث"}
    </Accordion.Toggle>
    <Accordion.Collapse eventKey="0">
      <Card.Body>
				<SearchForm/>
				</Card.Body>
			</Accordion.Collapse>
		</Card>
	</Accordion>
	<div  className="flex-container">
		{lands.map( (land) => { return(
	  
	  
	  <Link to={`${history.location.pathname}/${land.id}`} className="text-link">
		<div className='flex-item'>
		<div className='purpose-header p-1'>
		<h3>{`أرض ${land.purpose}`}</h3>
		</div>
		<img className='img-show' src={land.displayImage} />
		
		<div className='text-container'>
		<h4>{`الحي:${land.district}`}</h4>
		<h4>{`الفئة:${land.category}`}</h4>
		<h4>{`الحجم:${land.size}م`}</h4>
		<h4>{`السعر:${land.price}ريال`}</h4>
		</div>
		
		</div> 
	  </Link>
	  
      
		)})}
	</div>
	<Pages page={page} noOfPages={pages}/>
	</div>
	)
	
	
}

export default ShowLands