import React, { useState, useEffect } from 'react'
import { useHistory } from 'react-router-dom'
import Select from 'react-select'
import Options from './Options'
import {Form,Button} from 'react-bootstrap'

const toSelect = (array) => {
	return array.map(a => {return {value:a,label:a}})
}



const SearchForm = ()=>{
	const [district,setDistrict] = useState('')
	const [category,setCategory] = useState('')
	const [landType,setLandType] = useState('')
	const [sortBy,setSortBy] = useState('')
	const [order,setOrder] = useState('')
	const [purpose,setPurpose] = useState('')
	
	const history = useHistory()
	const path = history.location.pathname
	
	const handleSearch = (event) => {
		event.preventDefault()
		var searchQuery = 'search'
		if(district !== ''){
			searchQuery = searchQuery.concat(`&district=${district.value}`)
		}
		if(category !== ''){
			searchQuery = searchQuery.concat(`&category=${category.value}`)
		}
		if(landType !==''){
			searchQuery = searchQuery.concat(`&landType=${landType.value}`)
		}
		if(purpose !==''){
			searchQuery = searchQuery.concat(`&purpose=${purpose.value}`)
		}
		if(sortBy !==''){
			searchQuery = searchQuery.concat(`&sortBy=${sortBy.value}`)
		}
		if(order !== ''){
			searchQuery = searchQuery.concat(`&order=${order.value}`)
		}
		const newPath = path.split('=')[0] + '=1&' + searchQuery
		history.push(newPath)
	}
	
	return(
	<div>
	<Form onSubmit={handleSearch}>
	<Form.Group className="mb-3" >
	<Form.Label>{"اسم الحي"}</Form.Label>
	<Select className="text-success" options={toSelect(Options.districts.map( d => d.name))} defaultValue={district} onChange={setDistrict}/>
	{district !== '' && <Form.Label>{"الفئة"}</Form.Label>}
	{district !== '' && <Select className="text-success" options={toSelect(Options.districts.filter(d => d.name === district.value)[0].categories)}
		defaultValue={category} onChange={setCategory} />}
	</Form.Group>
	<Form.Group className="mb-3" >
	<Form.Label>{"نوع الأرض"}</Form.Label>
	<Select className="text-success" options={toSelect(Options.landTypes)} defaultValue={landType} onChange={setLandType}/>
	</Form.Group>
	<Form.Group className="mb-3" >
	<Form.Label>{"للبيع أم للإيجار أم للاستثمار"}</Form.Label>
	<Select className="text-success" options={toSelect(Options.purpose)} defaultValue={purpose} onChange={setPurpose}/>
	</Form.Group>
	<Form.Group className="mb-3" >
	<Form.Label>{"ترتيب حسب"}</Form.Label>
	<Select className="text-success" options={Options.sortBy} defaultValue={sortBy} onChange={setSortBy}/>
	{sortBy !== '' && <Form.Label>{"نوع الترتيب"}</Form.Label>}
	{sortBy !== '' && <Select className="text-success" options={Options.order}
		defaultValue={order} onChange={setOrder} />}
	</Form.Group>
	<Button type='submit'>{"بحث"}</Button>
	</Form>
	</div>
	)
}


export default SearchForm