import React, { useState, useEffect } from 'react'
import { useQuery, useMutation } from '@apollo/client'
import {ME} from './queries'
import {Table,Spinner} from 'react-bootstrap'
import UserPagePrivate from './UserPagePrivate'


const UserPage = ()=>{
	const result = useQuery(ME)
	if(result.loading){
		return (<Spinner animation="border" variant="success" />)
	}
	const user = result.data.me
	return (
	<div>
	<h2>{"معلومات الحساب"}</h2>
	<Table className='mb-3' striped bordered hover>
	<tbody>
	<tr>
	<td>{"اسم المستخدم"}</td>
	<td>{user.username}</td>
	</tr>
	<tr>
	<td>{"رقم الجوال"}</td>
	<td>{user.phone}</td>
	</tr>
	<tr>
	<td>{"تاريخ إنشاء الحساب"}</td>
	<td>{user.date}</td>
	</tr>
	<tr>
	<td>{"عدد الأراضي المضافة"}</td>
	<td>{user.noOfLands}</td>
	</tr>
	<tr>
	<td>{"عدد الفلل المضافة"}</td>
	<td>{user.noOfVillas}</td>
	</tr>
	<tr>
	<td>{"عدد العمائر المضافة"}</td>
	<td>{user.noOfBuildings}</td>
	</tr>
	</tbody>
	</Table>
	{user.userType === "admin" && <UserPagePrivate/>}
	</div>
	)
}


export default UserPage 