import React, { useState, useEffect } from 'react'
import { useQuery } from '@apollo/client'
import ModalImage from "react-modal-image"
import {ONE_BUILDING,ME} from './queries'
import ReactWhatsapp from 'react-whatsapp'
import {useParams} from 'react-router-dom'
import {Table} from 'react-bootstrap'
import ShowBuildingPrivate from './ShowBuildingPrivate'
import Options from './Options'
const ShowBuilding = ()=> {
	const id = useParams().id
	const result = useQuery(ONE_BUILDING,{variables:{id:id}})
	const meResult= useQuery(ME)
	if(result.loading || meResult.loading){
	  return (<div>Loading</div>)
    }
	
    const building = result.data.allBuildings[0]
	const me = meResult.data.me
	if(!building){
		return <div>{"حدث خطأ"}</div>
	}
	console.log(building)
    return(<div>
	
	<ModalImage className="show-image mb-5"
		small={building.displayImage}
		large={building.displayImage}
		alt="صورة العرض"
	/>
	<div className='information-container mb-5'>
	<h4>{`الحي:${building.district}`}</h4>
	<h4>{`الفئة:${building.category}`}</h4>
	<h4>{`الحجم:${building.size}م`}</h4>
	<h4>{`السعر:${building.price}ريال`}</h4>
	</div>
	{building.imagesOfBuilding[0] && <div>
	<h2>{"صور العمارة"}</h2>
		<div className="flex-container">
		{building.imagesOfBuilding.map(image => <ModalImage className="flex-item-img" 
		  small={image}
		  large={image}
		/>)}
		</div>
	</div>}
	
	
	{building.imagesOfFlats[0] && <div>
	<h2>{"صور الشقق"}</h2>
		<div className="flex-container">
		{building.imagesOfFlats.map(image => <ModalImage className="flex-item-img" 
		  small={image}
		  large={image}
		/>)}
		</div>
	</div>}
	

	{building.imagesOfPlan[0] && <div>
	<h2>{"صور مخطط البناء"}</h2>
		<div className="flex-container">
		{building.imagesOfPlan.map(image => <ModalImage className="flex-item-img" 
		  small={image}
		  large={image}
		/>)}
		</div>
	</div>}
	
	
	{(building.notes || building.notes !== '') && <p className="h5 m-5">{building.notes}</p>}
	<h2 className="mb-4">{"معلومات إضافية"}</h2>
	<Table className='mb-3' striped bordered hover>
	<tbody>
	<tr>
	<td>{"المساحة"}</td>
	<td>{building.size}</td>
	</tr>
	<tr>
	<td>{"عدد الشقق"}</td>
	<td>{building.noOfFlats}</td>
	</tr>
	<tr>
	<td>{"عرض الشارع"}</td>
	<td>{building.streetWidth}</td>
	</tr>
	<tr>
	<td>{"عدد الشوارع"}</td>
	<td>{building.noOfStreets}</td>
	</tr>
	<tr>
	<td>{"الغرض"}</td>
	<td>{building.landType}</td>
	</tr>
	<tr>
	<td>{"عمر العقار (بالسنين)"}</td>
	<td>{building.age === 0 ? "جديد" : building.age}</td>
	</tr>
	</tbody>
	</Table>
	<div className='container text-center'>
	{(me &&( me.id === building.userCreated && me.userType === 'admin')) && <ShowBuildingPrivate building={building}/> }
	<ReactWhatsapp className='p-4 btn btn-success center' 
	number={Options.phone} message="Hello World!!!">{"تواصل عبر الواتس"}</ReactWhatsapp>
	</div>
	</div>
	
	)
}

export default ShowBuilding