import React, { useState, useEffect } from 'react'
import Dropzone from 'react-dropzone'
import { useMutation } from '@apollo/client'
import { ADD_VILLA, UPLOAD_IMAGE} from './queries'
import { useHistory } from 'react-router-dom'
import Select from 'react-select'
import Options from './Options'
import {Form,Button,Alert} from 'react-bootstrap'
import Swal from 'sweetalert2'

const toSelect = (array) => {
	return array.map(a => {return {value:a,label:a}})
}

const AddVillaForm = ()=> {
	// Here are the queries 
	const [addVilla, result ] = useMutation(ADD_VILLA)
	const [uploadImage] = useMutation(UPLOAD_IMAGE)
	const history = useHistory()
	// Here is the property type variable conatains "villa" for villa type
	const type = 'villa'
	// Here are the variables for nesessary information regarding the land 
	const [size,setSize] = useState('')
	const [noOfRooms,setNoOfRooms] = useState('')
	const [noOfHalls,setNoOfHalls] = useState('')
	const [noOfBathrooms,setNoOfBathrooms] = useState('')
	const [streetWidth,setStreetWidth] = useState('')
	const [district,setDistrict] = useState('')
	const [category,setCategory] = useState('')
	const [landNo,setLandNo] = useState('')
	const [purpose,setPurpose] = useState('')
	const [displayImage,setDisplayImage] = useState(false)
	const [villaImages,setVillaImages] = useState(false)
	const [roomsImages,setRoomsImages] = useState(false)
	const [kitchenImages,setKitchenImages] = useState(false)
	const [bathroomsImages,setBathroomsImages] = useState(false)
	const [planImages,setPlanImages] = useState(false)
	const [noOfStreets,setNoOfStreets] = useState('')
	const [landType,setLandType] = useState('')
	const [price,setPrice] = useState('')
	const [age,setAge] = useState(0)
	const [ownerName,setOwnerName] = useState('')
	const [ownerPhone,setOwnerPhone] = useState('')
	const [notes,setNotes] = useState('')
	
	// Here are the error messages variables for each field above
	const [sizeMessage,setSizeMessage] = useState('')
	const [noOfRoomsMessage,setNoOfRoomsMessage] = useState('')
	const [noOfHallsMessage,setNoOfHallsMessage] = useState('')
	const [noOfBathroomsMessage,setNoOfBathroomsMessage] = useState('')
	const [streetWidthMessage,setStreetWidthMessage] = useState('')
	const [districtMessage,setDistrictMessage] = useState('')
	const [categoryMessage,setCategoryMessage] = useState('')
    const [landNoMessage,setLandNoMessage] = useState('')
	const [purposeMessage,setPurposeMessage] = useState('')
	const [displayMessage,setDisplayMessage] = useState('')
	const [imagesMessage,setImagesMessage] = useState('')
	const [noOfStreetsMessage,setNoOfStreetsMessage] = useState('')
	const [landTypeMessage,setLandTypeMessage] = useState('')
	const [priceMessage,setPriceMessage] = useState('')
	const [ownerNameMessage,setOwnerNameMessage] = useState('')
	const [ownerPhoneMessage,setOwnerPhoneMessage] = useState('')
	
	// Here is the function that will check each single information is correct before sending it 
	// to the backend
	const check = () =>{
		console.log('entred check')
		var isOkay = true 
		setSizeMessage('')
		if(size ==''){
			setSizeMessage(Options.messages.emptyFieldMessage)
			isOkay = false
		}else if(isNaN(size) || size < 1){
			setSizeMessage('يجب أن يكون الحجم رقما مناسبا')
			isOkay = false
		}
		setNoOfRoomsMessage('')
		if(noOfRooms ==''){
			setNoOfRoomsMessage(Options.messages.emptyFieldMessage)
			isOkay = false
		}else if(isNaN(noOfRooms) || noOfRooms < 1){
			setNoOfRoomsMessage('يجب أن يكون عدد الغرف رقما مناسيا')
			isOkay = false
		}
		setNoOfHallsMessage('')
		if(noOfHalls ==''){
			setNoOfHallsMessage(Options.messages.emptyFieldMessage)
			isOkay = false
		}else if(isNaN(noOfHalls) || noOfHalls < 1){
			setNoOfHallsMessage('يجب أن يكون عدد الصالات رقما مناسبا')
			isOkay = false
		}
		setNoOfBathroomsMessage('')
		if(noOfBathrooms ==''){
			setNoOfBathroomsMessage(Options.messages.emptyFieldMessage)
			isOkay = false
		}else if(isNaN(noOfBathrooms) || noOfBathrooms < 1){
			setNoOfBathroomsMessage('يجب أن يكون عدد الصالات رقما مناسبا')
			isOkay = false
		}
		setStreetWidthMessage('')
		if(streetWidth ==''){
			setStreetWidthMessage(Options.messages.emptyFieldMessage)
			isOkay = false
		}else if(isNaN(streetWidth) || streetWidth < 1){
			setStreetWidthMessage('يجب أن يكون طول الشارع رقما مناسبا')
			isOkay = false
		}
		setDistrictMessage('')
		if(district ==''){
			setDistrictMessage(Options.messages.emptyFieldMessage)
			isOkay = false
		}
		setCategoryMessage('')
		if(category =='' && district !==''){
			setCategoryMessage(Options.messages.emptyFieldMessage)
			isOkay = false
		}
		setLandNoMessage('')
		if(landNo ==''){
			setLandNoMessage(Options.messages.emptyFieldMessage)
			isOkay = false
		}else if(isNaN(landNo) || landNo < 1){
			setLandNoMessage('يجب أن يكون رقم الأرض مناسبا')
			isOkay = false
		}
		setPurposeMessage('')
		if(purpose =='' ){
			setPurposeMessage(Options.messages.emptyFieldMessage)
			isOkay = false
		}
		setNoOfStreetsMessage('')
		if(noOfStreets == ''){
			setNoOfStreetsMessage(Options.messages.emptyFieldMessage)
			isOkay = false
		}
		setLandTypeMessage('')
		if(noOfStreets == ''){
			setLandTypeMessage(Options.messages.emptyFieldMessage)
			isOkay = false
		}
		setPriceMessage('')
		if(price ==''){
			setPriceMessage(Options.messages.emptyFieldMessage)
			isOkay = false
		}else if(isNaN(price) || price < 1){
			setPriceMessage('يجب أن يكون السعر رقما مناسبا')
			isOkay = false
		}
		setOwnerNameMessage('')
		if(ownerName ==''){
			setOwnerNameMessage(Options.messages.emptyFieldMessage)
			isOkay = false
		}else if(!/[ ء-ي]+/g.test(ownerName) || ownerName.length > 40){
			setOwnerNameMessage('يجب أن يكون الاسم مناسبا')
			isOkay = false
		}
		setOwnerPhoneMessage('')
		if(ownerPhone ==''){
			setOwnerPhoneMessage(Options.messages.emptyFieldMessage)
			isOkay = false
		}else if(!/05[0-9]{8}/g.test(ownerPhone)){
			setOwnerPhoneMessage('يجب أن يكون رقم الجوال مناسبا')
			isOkay = false
		}
		
		
		return isOkay
	} 
	
	useEffect(async () => {
    if ( result.data ) {
      const id = result.data.addVilla.id
	  if(displayImage[0]){
	  await uploadImage({variables:{image:displayImage[0],property:type,place:'display',id:id}})
	  }
	  if(villaImages[0]){
	  for(let image of villaImages){
		  if(image){
			await uploadImage({variables:{image:image,property:type,place:'villa',id:id}})
		  }
	    }
	  }
	  if(roomsImages[0]){
	  for(let image of roomsImages){
		  if(image){
			await uploadImage({variables:{image:image,property:type,place:'room',id:id}})
		  }
	    }
	  }
	  if(kitchenImages[0]){
	  for(let image of kitchenImages){
		  if(image){
			await uploadImage({variables:{image:image,property:type,place:'kitchen',id:id}})
		  }
	    }
	  }
	  if(bathroomsImages[0]){
	  for(let image of bathroomsImages){
		  if(image){
			await uploadImage({variables:{image:image,property:type,place:'bathroom',id:id}})
		  }
	    }
	  }
	  if(planImages[0]){
	  for(let image of planImages){
		  if(image){
			await uploadImage({variables:{image:image,property:type,place:'plan',id:id}})
		  }
	    }
	  }
	  Swal.fire({
		title:'تم إضافة العقار بنجاح',
		text:'يمكنك الإطلاع عليه في قائمة العقارات',
		icon:'success',
      }).then((result)=>{
		  if(result.isConfirmed){
			   history.go(0)
		  }
	  })
    }
  }, [result.data]) 
	
	const submit = (event)=>{
		event.preventDefault()
		if(check()){
		addVilla({variables:{size:Number(size),noOfRooms:Number(noOfRooms),noOfHalls:Number(noOfHalls),noOfBathrooms:Number(noOfBathrooms),streetWidth:Number(streetWidth),district:district.value,
		category:category.value,landNo:Number(landNo),noOfStreets:Number(noOfStreets.value),landType:landType.value,price:Number(price),age:Number(age),purpose:purpose.value,ownerName:ownerName,ownerPhone:ownerPhone,notes:notes}})
		}
		else{
			Swal.fire({
			icon: 'error',
			title: 'حدث خطأ',
			text: "تأكد من أن البيانات المدخلة مناسبة"
		})
		}
		
	}
	
	
	return(
	<div>
	<Form onSubmit={submit} className='pb-4'>
	<Form.Group className="mb-3" >
	<Form.Label>{"الحجم"}</Form.Label>
	<Form.Control required className="mb-3" value={size} type="Number" onChange={ ({target}) => {setSize(target.value)}}/>
		{ sizeMessage !== '' && <Alert variant='danger'>{sizeMessage}</Alert>}
	</Form.Group>
	<Form.Group className="mb-3" >
	<Form.Label>{"عدد الغرف"}</Form.Label>
	<Form.Control required className="mb-3" value={noOfRooms} type="Number" onChange={ ({target}) => {setNoOfRooms(target.value)}}/>
	    { noOfRoomsMessage !== '' && <Alert variant='danger'>{noOfRoomsMessage}</Alert>}
	</Form.Group>
	<Form.Group className="mb-3" >
	<Form.Label>{"عدد الصالات"}</Form.Label>
	<Form.Control required className="mb-3" value={noOfHalls} type="Number" onChange={ ({target}) => {setNoOfHalls(target.value)}}/>
		{ noOfHallsMessage !== '' && <Alert variant='danger'>{noOfHallsMessage}</Alert>}
	</Form.Group>
	<Form.Group className="mb-3" >
	<Form.Label>{"عدد دورات المياه"}</Form.Label>
	<Form.Control required className="mb-3" value={noOfBathrooms} type="Number" onChange={ ({target}) => {setNoOfBathrooms(target.value)}}/>
		{ noOfBathroomsMessage !== '' && <Alert variant='danger'>{noOfBathroomsMessage}</Alert>}
	</Form.Group>
	<Form.Group className="mb-3" >
	<Form.Label>{"طول الشارع"}</Form.Label>
	<Form.Control required className="mb-3" value={streetWidth} type="Number" onChange={ ({target}) => {setStreetWidth(target.value)}}/>
		{ streetWidthMessage !== '' && <Alert variant='danger'>{streetWidthMessage}</Alert>}
	</Form.Group>
	<Form.Group className="mb-3" >
	<Form.Label>{"اسم الحي"}</Form.Label>
	<Select  className="mb-3" options={toSelect(Options.districts.map( d => d.name))} defaultValue={district} onChange={setDistrict}/>
		{ districtMessage !== '' && <Alert variant='danger'>{districtMessage}</Alert>}
	{district !== '' && <Form.Label>{"الفئة"}</Form.Label>}
	{district !== '' && <Select className="mb-3" options={toSelect(Options.districts.filter(d => d.name === district.value)[0].categories)}
		defaultValue={category} onChange={setCategory} />}
		{ (district !== '' && categoryMessage !== '') && <Alert variant='danger'>{categoryMessage}</Alert>}
	</Form.Group>
	<Form.Group className="mb-3" >
	<Form.Label>{"رقم الأرض"}</Form.Label>
	<Form.Control required className="mb-3" value={landNo} type="Number" onChange={ ({target}) => {setLandNo(target.value)}}/>
		{ landNoMessage !== '' && <Alert variant='danger'>{landNoMessage}</Alert>}
	</Form.Group>
	<Form.Group className="mb-3" >
	<Form.Label>{"صورة العرض"}</Form.Label>
	<Dropzone onDrop={acceptedFiles => setDisplayImage(acceptedFiles)} 
	accept="image/*"
	maxFiles={1}
	>
		{({getRootProps, getInputProps, isDragActive, isDragReject, rejectedFiles,acceptedFiles}) => (
		<section className="container border p-3">
			<div {...getRootProps()} className="container">
			<Form.Control {...getInputProps()} />
			{!isDragActive && 'قم بالنقر هنا أو قم بالسحب والإفلات لرفع صورة العرض'}
            {isDragActive && !isDragReject && "قم بالإفلات لرفع الصورة"}
            {isDragReject && "لا يقبل هذا الملف, تأكد من رفع صورة واحدة"}
			{acceptedFiles.length > 0 && <p className='alert alert-success mt-3'>{"تم رفع صورة العرض, انقر لإعادة الرفع"}</p>}
		</div>
		</section>
		)}
	</Dropzone>
	</Form.Group>
	<Form.Group className="mb-3" >
	<Form.Label>{"صور الفيلا"}</Form.Label>
	<Dropzone onDrop={acceptedFiles => setVillaImages(acceptedFiles)}
    accept="image/*"
	maxFiles={8}>
		{({getRootProps, getInputProps, isDragActive, isDragReject, rejectedFiles,acceptedFiles}) => (
		<section className="container border p-3">
			<div {...getRootProps()} className="container">
			<Form.Control {...getInputProps()} />
			{!isDragActive && 'قم بالنقر هنا أو السحب والإفلات لرفع الصور (8 صور كجد أقصى)'}
            {isDragActive && !isDragReject && "قم بالإفلات لرفع الصور"}
            {isDragReject && "لا يقبل هذا الملف, تأكد من رفع 8 صور كحد أقصى"}
			{acceptedFiles.length > 0 && <p className='alert alert-success mt-3'>{`تم رفع ${acceptedFiles.length} من الصور, انقر لإعادة الرفع`}</p>}
		</div>
		</section>
		)}
	</Dropzone>
	</Form.Group>
	<Form.Group className="mb-3" >
	<Form.Label>{"صور الغرف"}</Form.Label>
	<Dropzone onDrop={acceptedFiles => setRoomsImages(acceptedFiles)}
    accept="image/*"
	maxFiles={8}>
		{({getRootProps, getInputProps, isDragActive, isDragReject, rejectedFiles,acceptedFiles}) => (
		<section className="container border p-3">
			<div {...getRootProps()} className="container">
			<Form.Control {...getInputProps()} />
			{!isDragActive && 'قم بالنقر هنا أو السحب والإفلات لرفع الصور (8 صور كجد أقصى)'}
            {isDragActive && !isDragReject && "قم بالإفلات لرفع الصور"}
            {isDragReject && "لا يقبل هذا الملف, تأكد من رفع 8 صور كحد أقصى"}
			{acceptedFiles.length > 0 && <p className='alert alert-success mt-3'>{`تم رفع ${acceptedFiles.length} من الصور, انقر لإعادة الرفع`}</p>}
		</div>
		</section>
		)}
	</Dropzone>
	</Form.Group>
	<Form.Group className="mb-3" >
	<Form.Label>{"صور المطبخ"}</Form.Label>
	<Dropzone onDrop={acceptedFiles => setKitchenImages(acceptedFiles)}
    accept="image/*"
	maxFiles={8}>
		{({getRootProps, getInputProps, isDragActive, isDragReject, rejectedFiles,acceptedFiles}) => (
		<section className="container border p-3">
			<div {...getRootProps()} className="container">
			<Form.Control {...getInputProps()} />
			{!isDragActive && 'قم بالنقر هنا أو السحب والإفلات لرفع الصور (8 صور كجد أقصى)'}
            {isDragActive && !isDragReject && "قم بالإفلات لرفع الصور"}
            {isDragReject && "لا يقبل هذا الملف, تأكد من رفع 8 صور كحد أقصى"}
			{acceptedFiles.length > 0 && <p className='alert alert-success mt-3'>{`تم رفع ${acceptedFiles.length} من الصور, انقر لإعادة الرفع`}</p>}
		</div>
		</section>
		)}
	</Dropzone>
	</Form.Group>
	<Form.Group className="mb-3" >
	<Form.Label>{"صور دورات المياه"}</Form.Label>
	<Dropzone onDrop={acceptedFiles => setBathroomsImages(acceptedFiles)}
    accept="image/*"
	maxFiles={8}>
		{({getRootProps, getInputProps, isDragActive, isDragReject, rejectedFiles,acceptedFiles}) => (
		<section className="container border p-3">
			<div {...getRootProps()} className="container">
			<Form.Control {...getInputProps()} />
			{!isDragActive && 'قم بالنقر هنا أو السحب والإفلات لرفع الصور (8 صور كجد أقصى)'}
            {isDragActive && !isDragReject && "قم بالإفلات لرفع الصور"}
            {isDragReject && "لا يقبل هذا الملف, تأكد من رفع 8 صور كحد أقصى"}
			{acceptedFiles.length > 0 && <p className='alert alert-success mt-3'>{`تم رفع ${acceptedFiles.length} من الصور, انقر لإعادة الرفع`}</p>}
		</div>
		</section>
		)}
	</Dropzone>
	</Form.Group>
	<Form.Group className="mb-3" >
	<Form.Label>{"صور مخطط البناء"}</Form.Label>
	<Dropzone onDrop={acceptedFiles => setPlanImages(acceptedFiles)}
    accept="image/*"
	maxFiles={8}>
		{({getRootProps, getInputProps, isDragActive, isDragReject, rejectedFiles,acceptedFiles}) => (
		<section className="container border p-3">
			<div {...getRootProps()} className="container">
			<Form.Control {...getInputProps()} />
			{!isDragActive && 'قم بالنقر هنا أو السحب والإفلات لرفع الصور (8 صور كجد أقصى)'}
            {isDragActive && !isDragReject && "قم بالإفلات لرفع الصور"}
            {isDragReject && "لا يقبل هذا الملف, تأكد من رفع 8 صور كحد أقصى"}
			{acceptedFiles.length > 0 && <p className='alert alert-success mt-3'>{`تم رفع ${acceptedFiles.length} من الصور, انقر لإعادة الرفع`}</p>}
		</div>
		</section>
		)}
	</Dropzone>
	</Form.Group>
	<Form.Group className="mb-3" >
	<Form.Label>{"عدد الشوارع"}</Form.Label>
	<Select  className="mb-3" options={toSelect([1,2,3,4])} defaultValue={1} onChange={setNoOfStreets}/>
		{ noOfStreetsMessage !== '' && <Alert variant='danger'>{noOfStreetsMessage}</Alert>}
	</Form.Group>
	<Form.Group className="mb-3" >
	<Form.Label>{"الغرض"}</Form.Label>
	<Select  className="mb-3" options={toSelect(Options.landTypes)} defaultValue={landType} onChange={setLandType}/>
		{ landTypeMessage !== '' && <Alert variant='danger'>{landTypeMessage}</Alert>}
	</Form.Group>
	<Form.Group className="mb-3" >
	<Form.Label>{"للبيع أم للإيجار أم للاستثمار"}</Form.Label>
    <Select  className="mb-3" options={toSelect(Options.purpose)} defaultValue={purpose} onChange={setPurpose}/>
		{ purposeMessage !== '' && <Alert variant='danger'>{purposeMessage}</Alert>}
	</Form.Group>
	<Form.Group className="mb-3" >
	<Form.Label>{"سعر الفيلا"}</Form.Label>
	<Form.Control required  className="mb-3" value={price} type="Number" onChange={ ({target}) => {setPrice(target.value)}}/>
		{ priceMessage !== '' && <Alert variant='danger'>{priceMessage}</Alert>}
	</Form.Group>
	<Form.Group className="mb-3" >
	<Form.Label>{"عمر العقار (إذا كان جديدا فاترك الخانة فارغة)"}</Form.Label>
	<Form.Control  className="mb-3" value={age} type="Number" onChange={ ({target}) => {setAge(target.value)}}/>
	</Form.Group>
	<Form.Group className="mb-3" >
	<Form.Label>{"اسم المالك"}</Form.Label>
	<Form.Control required className="mb-3" value={ownerName} type="text" onChange={ ({target}) => {setOwnerName(target.value)}}/>
		{ ownerNameMessage !== '' && <Alert variant='danger'>{ownerNameMessage}</Alert>}
	</Form.Group>
	<Form.Group className="mb-3" >
	<Form.Label>{"رقم جوال المالك"}</Form.Label>
	<Form.Control required className="mb-3" value={ownerPhone} type="text" onChange={ ({target}) => {setOwnerPhone(target.value)}}/>
		{ ownerPhoneMessage !== '' && <Alert variant='danger'>{ownerPhoneMessage}</Alert>}
	</Form.Group>
	<Form.Group className="mb-3" >
	<Form.Label>{"معلومات إضافية (اختياري)"}</Form.Label>
	<Form.Control as="textarea" rows={3} className="mb-3" value={notes} onChange={ ({target}) => {setNotes(target.value)}}/>
	</Form.Group>
	<Button type='submit'>{"أضف الفيلا"}</Button>
	</Form>
	</div>
	)
}

	export default AddVillaForm