import {gql} from '@apollo/client'

export const LOGIN = gql`
  mutation login($phone: String!, $password: String!) {
    login(phone: $phone, password: $password)  {
      value
    }
  }
`

export const CREATE_USER = gql`
  mutation CreateUser($username: String!, $phone: String!, $password: String!, $code: String!) {
    createUser(username: $username, phone: $phone, password: $password, code: $code) {
      phone
  }
}
`
export const ADD_LAND = gql`
  mutation AddLandMutation($size: Int!, $district: String!, $category: String!, $landType: String!, $price: Int!,
  $height: Int, $width: Int, $streetWidth: Int, $landNo: Int, $purpose: String, $ownerName: String, $ownerPhone: String, $noOfStreets: Int,$notes: String) {
  addLand(size: $size, district: $district, category: $category, landType: $landType, price: $price, height: $height, width: $width, streetWidth: $streetWidth,
  landNo: $landNo, purpose: $purpose, ownerName: $ownerName, ownerPhone: $ownerPhone, noOfStreets: $noOfStreets,notes:$notes) {
    id
  }
}
`

export const ADD_VILLA = gql`
  mutation AddVilla($size: Int!, $noOfRooms: Int!, $district: String!, $category: String!, $price: Int!, $age: Int!, $landNo: Int, $noOfBathrooms: Int, $noOfHalls: Int, $noOfStreets: Int, $notes: String,$landType: String!
  , $purpose: String, $ownerName: String, $ownerPhone: String, $streetWidth: Int) {
  addVilla(size: $size, noOfRooms: $noOfRooms, district: $district, category: $category, price: $price, age: $age, landNo: $landNo, noOfBathrooms: $noOfBathrooms, noOfHalls: $noOfHalls, noOfStreets: $noOfStreets, notes: $notes
  , purpose: $purpose, ownerName: $ownerName, ownerPhone: $ownerPhone, landType: $landType, streetWidth: $streetWidth) {
    id
  }
}
`

export const ADD_BUILDING = gql`
  mutation AddBuilding($size: Int!, $district: String!, $category: String!, $price: Int!, $age: Int!, $landNo: Int, $noOfFlats: Int, $noOfStreets: Int, $notes: String, $landType: String!
  , $purpose: String, $ownerName: String, $ownerPhone: String, $streetWidth: Int) {
  addBuilding(size: $size, district: $district, category: $category, price: $price, age: $age, landNo: $landNo, noOfFlats: $noOfFlats, noOfStreets: $noOfStreets, notes: $notes
  , purpose: $purpose, ownerName: $ownerName, ownerPhone: $ownerPhone, landType: $landType, streetWidth: $streetWidth) {
    id
  }
}
`


export const UPLOAD_IMAGE = gql`
  mutation uploadImage($image: Upload!,$property: String!,$id: ID!,$place: String){
	  uploadImage(file: $image,,property:$property,id:$id,place:$place)
  }
`

export const ALL_LANDS = gql`
 query allLands($district: String,$category: String,$landNo: Int,$landType: String,
 $sortBy: String,$order: String,$page: Int,$purpose: String) {
  allLands(district:$district,category:$category,landNo:$landNo,landType:$landType,
  sortBy:$sortBy,order:$order,page:$page,purpose:$purpose,done:"false",show:"true") {
    id
	displayImage
	district
	category
	price
	size
	purpose
	landType
  }
}
`

